<template>
  <el-container>
  <el-header>
    <el-row>
      <el-button type="primary" @click="dialogVisible = true" round>New</el-button>
      <el-dialog title="Add Employee" :visible.sync="dialogVisible" :append-to-body="true" width="30%">
        <span>This is a form for Employee list</span>
        <span slot="footer" class="dialog-footer">
          <el-button @click="dialogVisible = false">Cancel</el-button>
          <el-button type="primary" @click="dialogVisible = false">Conform</el-button>
        </span>
    </el-dialog>
    </el-row>
  </el-header>
  <el-main>
    <el-table
    :data="tableData"
    border
    style="width: 80%"
    :row-class-name="tableRowClassName">
    <el-table-column

      prop="id"
      label="ID"
      width="180">
    </el-table-column>
    <el-table-column
      prop="firstName"
      label="First Name"
      width="180">
    </el-table-column>
    <el-table-column
      prop="lastName"
      label="Last Name"
      width="180">
    </el-table-column>
    <el-table-column
      prop="tel"
      label="Tel"
      width="180">
    </el-table-column>
    <el-table-column
      prop="date"
      label="DATE"
      width="180">
    </el-table-column>
    <el-table-column
      prop="address"
      label="Address"
      width="580">
    </el-table-column>
    <el-table-column
      fixed="right"
      label="Option"
      width="180">
      <template slot-scope="scope">
        <el-button @click="handleClick(scope.row), deleteEmployee(index)" type="text" size="small">DELETE</el-button>
        <el-button type="text" size="small">MODIFY</el-button>
        
      </template>
    </el-table-column>
  </el-table>
  </el-main>
</el-container>
  
</template>

<script>
  export default {
    methods: {
      handleClick(row) {
        console.log(row);
      },
      deleteEmployee(index){
        this.tableData.splice(index,1);
      } , 
      //:render-header="renderHeader"
      // renderHeader () {
      //   return (
      //     <div>
      //     <el-button size='small' on-click={()=>this.exportExcel()}> <span  class='el-icon-upload2'></span> ADD</el-button>
      //     </div>
      //     )
      // }
    },
    data() {
      
      const item = {
        id: '1',
        firstName: 'Chonghe',
        lastName: 'Ping',
        tel: '7138887878',
        date: '10-25-2020',
        address: '4800 Calhoun Rd, Houston, TX 77004',
      };
      return {
        tableData: Array(10).fill(item),
        dialogVisible:false,
        form: {
          id: '',
          firstName:''
        }

      };
    },

  }
</script>